@import '../../../node_modules/antd/es/style/themes/default.less';

.container {
    padding: 20px;
    height: 200%;
    background-color: #f0f2f5;
}
.avatarHolder {
    margin-bottom: 24px;
    text-align: center;
    & > img{
        width: 104px;
        height: 104px;
        margin-bottom: 20px;
    }
    .name {
        margin-bottom: 4px;
        color: @heading-color;
        font-weight: 500;
        line-height: 28px;
    }
}

.detail {
    p {
        position: relative;
        margin-bottom: 8px;
        padding-left: 24px;
        & :last-child {
            margin-bottom:0;
        }
    }
    .userInfoIcons {
        position: relative;
        height: 14px;
        width: 14px;
        margin-right: 4px;
    }
}

.team {
    .teamTitle {
        font-size: 16px;
        font-weight: 500;
        margin-bottom: 8px;
    }
    :global{
        .ant-col {
            margin-bottom: 8px;
        }
        .ant-avatar {
            margin-right: 4px;
        }
    }
    a {
        display: block;
        overflow: hidden;
        color: @text-color;
        white-space: nowrap;
        text-overflow: ellipsis;
        word-break: break-all;
        transition: color 0.3;
        &:hover {
            color: @primary-color;
        }
    }
}