@import '~antd/es/style/themes/default.less';
.applactionsCardList {
    margin-bottom: -24px;
    :global{
        .ant-card-meta{
            margin: 0;
        }
    }
    .cardInfo{
        margin-top: 16px;
        margin-left: 40px;
        &::before,
        &::after{
            display: table;
            content: '';
        }
        &::after{
           clear: both;
           height: 0;
           font-size: 0;
           visibility: hidden;
        }
        & > div {
            position: relative;
            width: 50%;
            float: left;
            text-align: left;
            p {
                margin: 0;
                font-size: 24px;
                line-height: 24px;
            }
            P:first-child{
                margin-bottom: 4px;
                font-size: 12px;
                line-height: 24px;
                color: @text-color-secondary;
            }
        }
    }
}